import backHome from '../components/backHome'
import wx from 'weixin-js-sdk'
import {mapGetters,mapActions}from'vuex'
  export default {
      name: "wish-detail",
    data(){
       return{
         adress:{
           postalAddressData:{}
         },
         detail:{
           products:[]
         }
       }
    },
    mounted(){
        document.title ="心愿单详情"
    },
    created(){
      //localStorage.setItem('token','Bearer eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxODkxMzM1MTg5MSIsInNjb3BlcyI6WyJVU0VSIl0sImlzcyI6Imh0dHA6Ly95dWFvcS5jb20iLCJpYXQiOjE1MjY0NjgxNDksImV4cCI6MTUyNjQ2OTk0OX0.4aziJFyV-BBPKeBhU7BikwihizKyHfSdD245Fll7JfcKtQWTFHOV-5ujpnxF4mPXTJ0bhh_CLOOs7piQv7BNeg')
      if(this.$route.query.token){
        localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
      }
      if(this.$route.query.refreshToken){
        localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
      }
      //localStorage.setItem('refreshToken','Bearer eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxMzcwNTE4ODM2MSIsInNjb3BlcyI6WyJST0xFX1JFRlJFU0hfVE9LRU4iXSwiaXNzIjoiaHR0cDovL3l1YW9xLmNvbSIsImp0aSI6IjM3MDVjMzc4LTcyMjktNDY5OC05ZmM5LTdhMjI0ZDhmNzRmZCIsImlhdCI6MTUyNDcwNjQyMiwiZXhwIjoxNTI1MzExMjIyfQ.lyOEF1mHwZodZzIIERW8Wr_OBI4ry_SUPVgG2pRwfElQo9YT9ZgWrxx3W0RyG-g125GNk6sWfg9s0S9NDVIwww')

      if(this.$route.query.wishId){
        if(this.$route.query.cs=='p'){
          setTimeout(()=>{
            this.detailWish({this:this,method:this.detailWish,data:{wishId:this.$route.query.wishId}})
          },2000)
        }
        else{
          this.showLoading()
            this.detailWish({this:this,method:this.detailWish,data:{wishId:this.$route.query.wishId}})
        }
      }
    },
    components:{
      backHome
    },
    methods:{
      ...mapActions(['detailWish','addressDetail','addItemToShoppingCart']),
      helpFr(obj){
        this.showLoading()
        // wx.miniProgram.navigateTo({
        //   url: '/pages/sureorder/index?orderType=wishCart&cartType=wishCart&wishId='+ obj.wishId +'&adressId='+obj.contactMechId
        // })
        // return
      let p =  this.detail.products.map(v => v.price)
      let id =   this.detail.products.map(v => v.productId)
      let v =  this.detail.products.map(v => 1)
        if(p.length==1){
          let data={
            add_product_id:id.join(),
            quantity:v.join(),
            price:p.join(),
            cartType:'buyCart'
          }
          this.addItemToShoppingCart({data:data,this:this,n:1})
        }
        else{
          let obj ={
            productIds:id.join(),
            productNums:v.join(),
            productPrices:p.join(),
            cartType:'buyCart'
          }
          this.addItemToShoppingCart({data:obj,this:this})
        }
      },
      seeOrder(){
        // wx.miniProgram.navigateTo({
        //   url:'/pages/webview/index?path=myOrder'
        // })
        this.$router.push('/myOrder')
      },
      goTo(){
        this.hideLoading()
        let _this = this
        wx.miniProgram.navigateTo({
          url: '/pages/sureorder/index?orderType=wishCart&cartType=buyCart&wishId='+_this.$route.query.wishId+'&adressId='+_this.detail.contactMechId + '&hideGift=Y'
        })
      },
      copy(data){
        this.addressDetail({this:this,data:{contactMechId:data.contactMechId}})
       this.detail = Object.assign({},data)
      },
      coprData(data){
        data.countyName = data.countyName||''
        data.cityName = data.cityName||''
        data.provinceName = data.provinceName|| ''
        data.ssq= data.provinceName+ ' ' + data.cityName + ' '+data.countyName
        this.adress = Object.assign({},data)
      }
    }
  }



